.outerDivAbooutMe {
    width: 100%;
    height: 500px;
    text-align: center;
    position: absolute;
}

.innerDivAbooutMe {
    width: 50%;
    height: 70%;
    position: absolute;
    margin: auto;
    top: -3%;
    left: 0;
    right: 0;
    bottom: 0;
}

@media only screen and (max-width: 600px) {
    .innerDivAbooutMe {
        width: 95%;
    }
}

.imgAboutMe {
    height: 200px;
    width: 195px;
}

.textAboutMe {
    color:rgb(50, 138, 118);
    font-size: 1.2em;
    font-weight: 600;
    font-family: "Comic Sans MS" cursive sans-serif;
}

@media only screen and (max-width: 600px) {
    .textAboutMe {
        font-size: 16px;
    }
}

.mainAboutMe {
    padding: 50px 50px 0px 50px; 
}

@media only screen and (max-width: 600px) {
    .mainAboutMe {
        padding: 50px 20px 0px 20px; 
    }
}

