/**********************************************/
/***************Front end info******************/
/**********************************************/

.outerDivfrondEndInfo {
    width: 100%;
    height: 100%;
    text-align: center;
    position: absolute;
    /* background-color: rgb(139, 133, 133); */
}

.innerDivfrondEndInfo {
    width: 60%;
    height: 100%;
    position: absolute;
    margin: auto;
    top: 0;
    left: 40%;
    right: 0;
    bottom: 0;
    /* background-color: whitesmoke; */
}

@media only screen and (max-width: 600px) {
    .innerDivfrondEndInfo {
        width: 90%;
        left: 0;
    }
}

.textfrondEndInfo {
    position: absolute;
    margin: auto;
    top: -100px;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100px;
    background-color: white;
}

@media only screen and (max-width: 600px) {
    .textfrondEndInfo {
        padding: 0 0 0 0;
        top: -50px;
    }
}

.outerDivfrontEndTools {
    width: 100%;
    height: 100%;
    text-align: center;
    position: absolute;
    /* background-color: rgb(240, 21, 21); */
}

@media only screen and (max-width: 600px) {
    .outerDivfrontEndTools {
    }
}

.innerDivfrontEndTools {
    width: 120px;
    height: 120px;
    position: absolute;
    margin: auto;
    top: 0;
    left: 0;
    right: 40%;
    bottom: 0;
    background-color: #e0ebe8;;
    border-radius: 50%;
}

@media only screen and (max-width: 600px) {
    .innerDivfrontEndTools {
        width: 140px;
        height: 140px;
        right: 0;    
    }
}

.imgfrontEndTools {
    position: absolute;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

