hr {
    position: relative;
    margin-top: 13%;
    width: 10%;
    border: 5px dashed rgb(50, 138, 118);
    border-radius: 5px;
    z-index: 5;
}

@media only screen and (max-width: 600px) {
    .innerDivbackEndTools {
        width: 140px;
        height: 140px;
        left: 0;
    }

    hr {
        margin-top: 22%;
    }
}

@media only screen and (min-width: 1800px) {

    hr {
        display: none;
    }
}

